html,
body {
  box-sizing:border-box;
  -moz-box-sizing:border-box; /* Firefox */
  -webkit-box-sizing:border-box; /* Safari */
}

html {
  overflow-y: scroll;
}

div {
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

select {

  appearance:none;
  -moz-appearance:none;
  -webkit-appearance:none;

  &::-ms-expand {
    display: none;
  }

}

.draggable_panel {
  border: 1px solid #777777;
  height: 100%;
}

.draggable_header {
  display: none;
  cursor: move;
  height: 0px;
  width: 100%;
}
